<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>

      <span class="content__info">
         :{{ totalAmounts }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" method="POST"
            @submit.prevent="sendOrder"
      >
        <div class="cart__field">
          <div class="cart__data">

            <input-form-field
              v-model="formFields.name"
              :error="formErrors.name"
              placeholder="   "
              title=""
            >
            </input-form-field>

            <input-form-field
              v-model="formFields.address"
              :error="formErrors.address"
              placeholder="  "
              title=" "
            >
            </input-form-field>

            <input-form-field
              v-model="formFields.phone"
              :error="formErrors.phone"
              placeholder="  "
              title=""
              type="tel"
            >
            </input-form-field>

            <input-form-field
              v-model="formFields.email"
              :error="formErrors.email"
              placeholder="  Email"
              title="Email"
              type="email"
            >
            </input-form-field>

            <textarea-form-field
              v-model="formFields.comments"
              :error="formErrors.comments"
              placeholder=" "
              title="  "
            >
            </textarea-form-field>

          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="delivery in deliveries" :key="delivery.id">
                <label class="options__label">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="delivery"
                         :value="delivery.id"
                         v-model="deliveryTypeId"
                         :checked="delivery.id === deliveryTypeId"
                  >
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.cutePrice }}</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="payment in payments" :key="payment.id">
                <label class="options__label">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="pay"
                         :value="payment.id"
                         v-model="paymentTypeId"
                         :checked="payment.id===paymentTypeId"
                  >
                  <span class="options__value">
                    {{ payment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <cart-block :products="products"
                    :total-sum="totalSum"
                    :total-amounts="totalAmounts"
                    :cuteDeliveryType="cuteDeliveryType"
                    :deliveryPrice="deliveryPrice"
        >
          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </cart-block>

        <div class="cart__error form__error-block" v-show="errorMessage">
          <h4>  !</h4>
          <p>
            {{ errorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
import { computed, ref, watch } from 'vue';
import { useStore } from 'vuex';
import InputFormField from '@/components/InputFormField.vue';
import TextareaFormField from '@/components/TextareaFormField.vue';
import CartBlock from '@/components/CartBlock.vue';
import useOrder from '@/hooks/useOrder.vue';

export default {
  components: { InputFormField, TextareaFormField, CartBlock },
  setup() {
    const $store = useStore();
    const products = computed(() => $store.getters.cartDetailsProducts || []);
    const totalAmounts = computed(() => $store.getters.cartTotalAmounts || 0);
    const totalSum = computed(() => $store.getters.cartTotalSum || 0);
    const deliveryPrice = ref(0);
    const cuteDeliveryType = ref('');
    const {
      formFields,
      formErrors,
      errorMessage,
      deliveries,
      deliveryTypeId,
      payments,
      paymentTypeId,
      fetchDeliveries,
      fetchPayments,
      sendOrder,
    } = useOrder();
    const fetchBasket = () => {
      $store.dispatch('loadBaskets');
    };
    fetchBasket();
    fetchDeliveries();
    watch(deliveryTypeId, () => {
      fetchPayments();
      const delivery = deliveries.value.find((item) => item.id === deliveryTypeId.value);
      deliveryPrice.value = delivery.price;
      cuteDeliveryType.value = `${delivery.title}: ${delivery.price}`;
    });

    return {
      formFields,
      formErrors,
      totalAmounts,
      totalSum,
      errorMessage,
      products,
      deliveries,
      payments,
      paymentTypeId,
      deliveryTypeId,
      deliveryPrice,
      cuteDeliveryType,
      sendOrder,
    };
  },
};
</script>
