<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{ name: 'main', params : { categoryId: category.id } }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img :src="product.img" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ currentOffer.id }}</span>
        <h2 class="item__title">
          {{ currentOffer.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST">
            <b class="item__price">
              {{ currentOffer.productPrice }} 
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ul class="colors">

                <li class="colors__item" v-for="(item, key) in colors" :key="key">
                  <label class="colors__label">
                    <input class="colors__radio sr-only"
                           type="radio"
                           name="color-item"
                           :value="item.color.id"
                           v-model="currentColorId"
                           :checked="item.color.id===currentColorId"
                    >
                    <span class="colors__value"
                          :style="'background-color: ' + item.color.code + ';'">
                    </span>
                  </label>
                </li>

              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">{{ product.title }}</legend>

              <ul class="sizes sizes--primery">
                <label class="sizes__item" v-for="item in product.offers" :key="item.id">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only"
                           type="radio" name="sizes-item"
                           :value="item.id"
                           v-model="productOfferId"
                    >
                    <span class="sizes__value">
                      {{ item.propValues[0].value }}
                    </span>
                  </label>
                </label>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button type="button"
                        aria-label="  "
                        @click.prevent="doDecrementProduct">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">

                <button type="button"
                        aria-label="  "
                        @click.prevent="doIncrementProduct"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" @click.prevent="doAddToCart">
                 
              </button>
            </div>
            <p v-show="productAdded">
                  !
            </p>

          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">

          <a href="#">
             
          </a>

          <div v-for="item in product.specifications" :key="item.id">
            <h3>{{ item.title }}</h3>
            <p>{{ item.value }}</p>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { ref, watch } from 'vue';
import useProduct from '@/hooks/useProduct.vue';

export default {
  setup() {
    const productAdded = ref(false);
    const productAmount = ref(1);

    const {
      product,
      category,
      colors,
      currentColorId,
      currentOffer,
      productOfferId,
      fetchProduct,
      addToCart,
      fetchOffer,
    } = useProduct();

    const doIncrementProduct = () => {
      productAmount.value += 1;
    };
    const doDecrementProduct = () => {
      if (+productAmount.value > 1) {
        productAmount.value -= 1;
      }
    };
    const doAddToCart = () => {
      if (productAmount.value > 0) {
        addToCart({
          currentProductOfferId: productOfferId.value,
          colorId: currentColorId.value,
          quantity: productAmount.value,
        });
        productAdded.value = true;
      } else {
        productAmount.value = 1;
      }
    };
    watch(productOfferId, (newValue) => {
      fetchOffer(newValue);
    });
    watch(productAmount, (newValue) => {
      if (newValue < 0) {
        productAmount.value = 1;
      }
    });
    fetchProduct();
    return {
      productAdded,
      product,
      productAmount,
      category,
      colors,
      productOfferId,
      currentColorId,
      currentOffer,
      doIncrementProduct,
      doDecrementProduct,
      doAddToCart,
    };
  },

};
</script>
<style scoped lang="stylus">
.item__pics img
  max-height: 70vh;
</style>
